.header{
      width: 100%;
      height: 8rem;
      background-color: rgb(22, 26, 24);
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 1.5rem;
      color: white;
      font-size: 1.7rem;
      & ul {
            width: 350px;
            margin-right: 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;
          
      }
      ul li{
            list-style: none;
      }
      ul  li a {
            text-decoration: none;
            color: white;
            font-size: 3rem;
      }
      ul  li a p{
            font-size: 4rem;
            position: relative;
            & span{
                  position: absolute;
                  font-size: 19px;
                  top:17px;
                  left: 15px;
                  color: red;
                
            }
      }
   
}